import Head from 'next/head';
import React, {useState, useEffect} from 'react';
import Container from 'react-bootstrap/Container';
import Col from 'react-bootstrap/Col';
import Row from 'react-bootstrap/Row';
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import Comparison from '../components/comparison.js';

export default function Home() {
    const [countList, setCountList] = useState([]);

    // useEffect(() => {

    // },[   ])

    const increaseCount = () => {
        console.log('increaseCount')
        countList.push(1);
        const tmpCount = JSON.parse(JSON.stringify(countList));
        setCountList(tmpCount);
    }

    const decreaseCount = () => {
        countList.pop();
        const tmpCount = JSON.parse(JSON.stringify(countList));
        setCountList(tmpCount);
    }

    return (
        <div>
            <Head>
                <title>STT Benchmark</title>
            </Head>
            <Container fluid>
                <Row>
                    <Col>
                        <h1>STT Benchmarking</h1>
                        <p>You clicked { countList.length }  times</p>

                        

                        <Form>
                          <Form.File 
                            id="custom-file"
                            label="Select base text  file"
                            custom
                          />
                        </Form>
                    </Col>
                    <Col>
                        <video style={ {  width: '100%',  height: '200px' }  }  controls></video>
                    </Col>
                </Row>

            </Container>
            <Container fluid>
                <Row>
                    <Col>
                        <Button block
                            onClick={increaseCount}>
                            Add One
                        </Button>
                    </Col>
                    {/* <Col>
                        <Button block onClick={decreaseCount}>
                            Remove One
                        </Button>
                    </Col> */} </Row>
                <Row> {/* <Col>Base Text</Col> */}
                    {
                    countList.map((el) => {
                        return <Col>{el}<Comparison/></Col>
                })
                } </Row>
            </Container>
        </div>
    )
}


export async function getStaticProps(context) {
    return {
        props: {}, // will be passed to the page component as props
    }
}
